.calculator-container{
    width: 250px;
    height: 250px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
    gap:10px;
    padding: 10px;
   margin: auto;
   margin-top: 10%;
    background-color: white;
    border-radius: 20px;
    box-shadow: -15px 15px 40px 5px #f992d7cd,
                15px -15px 40px 5px #af89eecd;
    input{
        width: 230px;
        padding: 5px 0;
        outline: 1px solid gray;
    }
}
.operator-container{
    display: flex;
   align-items: center;
   gap:20px;
   .btn{
    width: 45px;
    border: none;
    padding: 10px;
    background-color: black;
    color: white;
    cursor: pointer;
   }
}
.error{
    color: red;
}
.success{
    color:#0057ff;
}